.mail-list {
    height: 1200px;
    overflow-y: scroll;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 40px;
  }
  .headingw {
    font-size: 40px; /* Adjust the font size as needed */
    font-weight: bold; /* Make the text bold */
    color: #35ab41; /* Set the text color */
    text-align:center ;
    /* Add more styles as needed */
  }
  
  .box {
    padding: 20px;
    width: 80%;
    background-color: #77cb79; /* Light gray background */
    margin-bottom: 10px;
    text-align: center;
    margin-top: 20px;
    border-radius: 5px;
    border-color: #185c04;
  }
  
  .box:last-child {
    border-bottom: none;
    margin-bottom: 0;
  }
  
  strong {
    font-weight: bold;
  }
  
  p {
    margin: 5px 0;
    font-size: 16px;
    color: #333; /* Dark gray text color */
  }
  
  .date {
    font-size: 14px;
    color: #888; /* Light gray date color */
  }
  